<template>
  <div class="signup">
    
      <h1 class="subtitle">Welcome to Sign up Page</h1>

        <div >
      
            <input class="input is-size-3" v-model="username" placeholder="Username.."/>
            <br>
            <br>
            
            <input class="input is-size-3" v-model="password" type="password" placeholder="Password..."/>
            <br>
            <br>
            <input class="input is-size-3" v-model="confirmpassword" type="password" placeholder="Confirm Password..."/>     
            <br>
            <br>
            <input class="button is-primary is-medium" @click="signup()" type="submit" value="sign up"/>
            
        </div>
        <br>
      
        <p>Already have an account?</p>  
        <button class="button is-link" @click="goToLogin()">Login</button>
  </div>
</template>

<script>
import axios from "axios";
// @ is an alias to /src


export default {

  name: 'Sign Up',
  data() {
    return {
      username: "",
      password: "",
      confirmpassword: ""
    }
  },
  methods: {
    signup(){

      if(this.username.length <5){
        return alert("username too short!")
      }
      if(this.password.length <5){
        return alert("password too short!")
      }
      if(this.password === this.confirmpassword){
        axios.post("/users/create", {
          name: this.username,
          password: this.password
        })
        .then((res) => {
          alert(res.data)
          this.$router.replace('/login')
        })
        .catch((e) => {
          console.log(e)
        })
      } else {
        alert("Password didnt match")
      }
    },
    goToLogin(){
      this.$router.replace('/login')
    }
  }

}
</script>
