<template>
  <div>
      <h1>{{user}}</h1>
      <ul v-for="tweet in tweets" :key="tweet.id">
         <div class="card">
          <li>
              <h3 class="title is-size-5">{{tweet.tweet}}</h3>
           
              <h5 class="subtitle is-size-5">@{{tweet.username}}</h5>
              <br>
              <h5 class="subtitle is-size-6">{{tweet.time}}</h5>
              <br>
              <br>
          </li>
         </div>
      </ul>
      <button class="button is-primary" @click="nextPage()">Next Page</button>{{page/5+1}}
  </div>
</template>

<script>
import axios from "axios";

export default {
    name: "User",
    data(){
        return {
            user: this.$route.params.user,
            tweets: [],
            page: 0
        }
    },
    created(){
        const token = localStorage.getItem('token')
        if (token) {
        axios.defaults.headers.common['Authorization'] = "Bearer " + token
        } else {
            this.$router.replace('/login')
        }

        axios.post(`/tweets/${this.user}?page=${this.page}`)
        .then((res) => {
            this.tweets = res.data.tweetsOfOne
        })
        .catch((e) => {
            console.log(e)
        })
    },
    methods: {
        
        nextPage(){
            this.page += 5 
            axios.post(`/tweets/${this.user}?page=${this.page}`)
            .then((res) => {
                this.tweets = res.data
            })
            .catch((e) => {
                console.log(e)
            })
        }
    }

}
</script>

