<template>
  <div>
      <h1>Find Users</h1>
    
      <ul v-for="user in users" :key="user.id">
          
         
          <li>
              <button class="button" @click="goToUser(user.user)">{{user.user}}
              </button>
              
             <button class="button is-primary" v-if="user.following == false" @click="follow(user.user)">Follow</button>
             <button class="button is-danger" v-if="user.following == true" @click="unfollow(user.user)">UnFollow</button> 
            <br>
            <br>

          </li>
      </ul>
  </div>
</template>

<script>
import axios from 'axios';

export default {
    name: "Find Users",
    data(){
        return {
            users: []
        }
    },
    created(){
        const token = localStorage.getItem('token')
        if (token) {
        axios.defaults.headers.common['Authorization'] = "Bearer " + token
        } else {
            this.$router.replace('/login')
        }


        axios.get("/users/findusers")
        .then((res) => {
            this.users = res.data.newResponse
            console.log(res.data)
            
        })
    },

    methods: {
        follow(user){
            axios.post('/following/follow',{
                following: user
            })

            axios.get("/users/findusers")
            .then((res) => {
                this.users = res.data.newResponse
            })
        }
        
        ,
        async  unfollow(user){

            axios.post('/following/unfollow',{
                following: user
            })

           
            axios.get("/users/findusers")
            .then((res) => {
                this.users = res.data.newResponse
            })

        },
        goToUser(user){
            this.$router.replace(`/user/${user}`)
        }
    }

}
</script>

