<template>
  <div >
      <input v-model="tweet" placeholder="What's on your mind?">
      <br>
      <button @click="postTweet()" >Post</button>
      
  </div>
</template>

<script>
import axios from "axios";

export default {
    name: "Post Tweet",
    data(){
        return {
            tweet: ""
        }
    },
    methods: {
        postTweet(){
            const token = localStorage.getItem('token')
            if (token) {
            axios.defaults.headers.common['Authorization'] = "Bearer " + token
            } else {
                this.$router.push('/login')
            }


            if (this.tweet.length> 160){
                return alert("max  characters: 160")
            }
            if(this.tweet.length < 1){
                return alert("tweet too short!")
            }
            axios.post("/tweets", {
                tweet: this.tweet
            })
            .then((res) => {
                alert(res.data.message)
               // console.log(res.data)
                
            })
            .catch((e) => {
                console.log(e)
            })
            this.$emit('change-show-tweet')
        }
    }
}
</script>

<style lang="scss" scoped>
input {
    
    width: 20em;
    height: 5em;
}
</style>

