<template>
  <div>
      <h1 class="title is-size-5">{{name}}</h1>
    <h3 class="title is-size-5">My Profile</h3>
    <ul v-for="tweet in tweets" :key="tweet.id">
        <li>
            <div class="card">
           <h3 class="title is-size-5">Tweet: {{tweet.tweet}}</h3>
           <h5 class="subtitle is-size-5">{{tweet.time}}</h5>
           <br>
           <br>
            </div>
            
        </li>

    </ul>
    <button class="button is-link" @click="nextPage()">next page</button>{{page/5+1}}
  </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'My Profile',
    data(){
        return {
            tweets: [],
            page:0,
            name: null

        }
    },
    created(){
        const token = localStorage.getItem('token')
        this.name = localStorage.getItem('name')
        if (token) {
        axios.defaults.headers.common['Authorization'] = "Bearer " + token
        } else {
            this.$router.replace('/login')
        }
        axios.get(`/tweets/tweetsofmine?page=${this.page}`)
        .then((res) => {
            this.tweets = res.data.tweetsOfOne
        })
        .catch((e) => {
            console.log(e)
        })
    },
    methods:{
        nextPage(){
            this.page+=5
            axios.get(`/tweets/tweetsofmine?page=${this.page}`)
            .then((res) => {
                this.tweets = res.data
            })
            .catch((e) => {
                console.log(e)
            })
        }
    }
}
</script>

