<template>
  <div>
      <h1 class="title">Welcome to Homepage</h1>
      <h2 class="title is-red">@{{name}}</h2>
      <PostTweet class="button" @change-show-tweet="showPostTweet = false" :showPostTweet="showPostTweet" v-if="showPostTweet == true"/>
      <button v-if="showPostTweet==false" @click="showPostTweet=true">Create tweet</button>
      <br>
      <br>        
      <ul  v-for="tweet in tweets" :key="tweet.id">
        <li >
            <div class="card">
          <p class="title is-5">{{tweet.tweet}}</p>        
          <p class="subtitle is-6">@{{tweet.username}}</p>
            <time class="title is-6">Time: {{tweet.time}}</time>    
            </div>
            <br>
            <br>
            <br>
        
        </li>
      </ul>
      
          <p class="title is-size-6">current page: {{page/5+1}}</p>
          <button class="button is-link" @click="nextPage()"> Next Page </button>
      <button class="button is-danger" @click="logOut()">Log Out</button>
  </div>
</template>

<script>
import axios from 'axios';
import PostTweet from "../components/PostTweet.vue"

export default {
    name: "Home",
    data(){
        return {
            tweets: [],
            page: 0,
            showPostTweet: false,
            name: null
        }
    },
    components:{
        PostTweet
    },
    mounted () {
        const token = localStorage.getItem('token')
        this.name = localStorage.getItem('name')
        if (token) {
        axios.defaults.headers.common['Authorization'] = "Bearer " + token
        } else {
            this.$router.replace('/login')
        }
        axios.get(`/tweets/tweetsofmany?page=${this.page}`)
        .then((res) => {
            this.tweets = res.data.tweetsOfMany
            console.log(res.data)
            if(res.data.message == "error occured at verification"){
                this.$router.replace('/login')
            }
        })
        .catch((err) => {
            console.log(err)
        })
    },
    methods: {
        logOut(){
            localStorage.removeItem('token')
            axios.defaults.headers.common['Authorization'] = "Bearer " +"null"
            this.$router.go('/login')
        },
        nextPage(){
            this.page += 5;
            axios.get(`/tweets/tweetsofmany?page=${this.page}`)
            .then((res) => {
                this.tweets = res.data
            })
            .catch((err) => {
                console.log(err)
            })
        }
    }

}
</script>

